    p(data-aos="fade-down-right") El proceso de prototipado consiste en elaborar una representacin visual de los componentes del <i>software</i> y este proceso da como resultado la creacin de subproductos segn el nivel de fidelidad visual respecto al producto final, de esta forma podemos definir los prototipos de la siguiente forma: #[i mockups] (alta fidelidad) y #[i wireframe] (es una representacin de baja o media fidelidad).  En algunos equipos de trabajo los desarrolladores usan el nombre prototipado para referirse a una simulacin que ofrece algn tipo de interaccin con el usuario, para todos los casos existen muchas herramientas que permiten la creacin de alguno de estos subproductos tal vez el ms popular para crear #[i mockups, wireframe] o prototipos con interaccin, es Sketch, aunque  solo es para Mac, es la que mejor calidad de prototipados ofrece actualmente. 

