      h2 1.2  Maquetacin del sitio web 
    p(data-aos="fade-down-right") El proceso de maquetacin web es la transformacin del diseo o prototipo a un producto web en un conjunto de archivos (HTML, CSS, JS) de manera que sern desplegados (renderizados) por un navegador. 
