                p.pb-5(data-aos="fade-down-right") #[i Mobile first] los mviles primero, es la estrategia de diseo que refiere a una maquetacin que tenga en cuenta, en primera instancia, el despliegue en dispositivos mviles. Pantallas reducidas en relacin con las tablets o los monitores que usamos comnmente con los ordenadores, y tras tener la maqueta preparada para mviles, realizar un escalado y hacer otra dimensin de pantallas, es decir, aumentar el tamao y adaptarlo a una pantalla de escritorio.
              .col-lg-4
