    p(data-aos="fade-down-right") Bootstrap es un conjunto de herramientas de estilos CSS de cdigo abierto, para que los desarrollos web se puedan usar con HTML, CSS y JavaScript. Con l se puede dar forma al sitio o pginas web usando sus libreras CSS y JavaScript. Incluye diferentes componentes: ventanas modales, mens, cuadros, botones, formulario, es decir, los elementos que se necesitan para maquetar una pgina. A continuacin, se presenta la maquetacin de un formulario. 

    .row.justify-content-center.align-items-center   
