            h5 Lo material como una metfora
            p Como lo material est unido a la realidad fusionndose con el espacio, tiempo y movimiento, y se vuelve tctil, es por esto que se puede integrar con la tecnologa haciendo uso de la creatividad e imaginacin.
            p Los bordes y superficies de las cosas materiales entregan pistas visuales basadas en la realidad, y el uso de atributos familiares al mundo real ayuda al usuario a entender las posibilidades de lo que se puede hacer con cada elemento presente en la interfaz. Para lograr esto se deben presentar los principios superficie, luz y movimiento como elementos principales para transmitir cmo los objetos existen, estn dispuestos e interactan uno con el otro o con el usuario.    
          .col-lg-4 
            img.img-t.img-a(data-aos="zoom-in")(src='@/assets/ilustraciones/img11.svg', alt='')
            p.text-center #[b Materiales]  

