            p Todos los elementos visuales son diseados por los conceptos de la teora del diseo como el uso del color, los espacios en blanco, sombras, el sistema de tabla de datos, etc. Todos estos elementos ayudan a determinar la jerarqua y lectura visual.
            img.img-t.img-a(data-aos="zoom-in")(src='@/assets/ilustraciones/img12.svg', alt='')

      .tarjeta.color-primario--borde.p-4(titulo="Sistema de color de material Design")
        .row.justify-content-start.align-items-center    
          .col-lg-12
            h5 Sistema de color de material Design 
            p Ayuda a aplicar color a las interfaces de usuario de una manera simplificada. En este sistema, el diseador define un color como primario y otro como secundario para representar el tema de su marca.
            p Las variaciones de oscuros y claros para cada color se aplican a la interfaz de usuario de diferentes maneras de manera preestablecida.
            img.img-t.img-a(data-aos="zoom-in")(src='@/assets/ilustraciones/img13.svg', alt='')

      .tarjeta.color-primario--borde.p-4(titulo="El movimiento otorga significado")
        .row.justify-content-start.align-items-center    
          .col-lg-12
            h5 El movimiento otorga significado
            p En una aplicacin web o mvil una animacin capta la atencin de los usuarios, sin embargo, es deseable que estas animaciones no interrumpan la experiencia de usuario; as es deseable que exista relacin entre las animaciones y la propia esencia del sitio web o aplicacin, por ejemplo, el efecto de presionar el botn la experiencia real de desplazar un elemento.
            p Para Google, el movimiento enfatiza el papel del usuario como el actor principal de la interaccin con la mquina y el que dirige los movimientos en dicha interaccin; y las acciones del usuario tienen el poder de cambiar el diseo o disposicin de los elementos. Es as como los elementos de la interfaz son presentados al usuario sin interferir con su experiencia, incluso cuando dichos elementos se transforman y reorganizan.
            p En conclusin, Material Design es una propuesta de diseo novedosa y que est incorporada en las nuevas versiones de Android. Los principios del Material Design van guiados a mejorar la experiencia de usuario teniendo en cuenta las ltimas tecnologas. 
            
