          .row.justify-content-center.align-items-center(numero="3" titulo="Maquetacin Bootstrap")
            .col-11
              p Bootstrap fundamenta su maquetacin con base en grillas que son la divisin mediante lneas horizontales y verticales que son iguales en su separacin y tamao.
              p El esquema de pgina que nos propone Bootstrap est compuesto sistemas de grillas basadas en filas y columnas. En cada fila est definida en 12 columnas y cada columna posee un ancho relativo a ese nmero 12. Es decir, si el ancho total es de 564px el tamao de cada columna es 560px/12 = 47px, por tanto, cada columna ser de tamao 47px. Este valor no es fijo, pues las columnas se redimensionarn siempre que se cambie el tamao o disposicin de la ventana o pantalla.
              p El sistema de grilla de Bootstrap puede adaptarse a los seis puntos de interrupcin que representa las dimensiones de pantallas ms usados. Los seis niveles de cuadrcula predeterminados son los siguientes:
              .list.mt-4
                .list__item
