                .list__txt 
                    span Extraextragrande (xxl): para monitores, smart-tv de alta resolucin.                 

          .row.justify-content-center.align-items-center(numero="4" titulo="Creacin de una fila para un contenedor la clase row")
            .col-11
              .cajon.color-acento-botones.p-4.my-4
                img.img-t(src='@/assets/ilustraciones/img81.svg', alt='') 
  
              p Cada divisor de clase <i>row</i>, puede contener cuantos divisores se deseen, solo que para dimensionar el tamao que ocupa cada uno, en un sistema de 12 columnas, se deben usar las clases que definen el tamao con nmero de 1 a 12, col-1, col-2, col-*, donde * va de 1 a 12.  Siempre es deseable que la suma de estos divisores columna sea 12 o mltiplos de 12.
              p A continuacin, un ejemplo: 
