              p Para que se los contenidos se visualicen de mejor manera en un navegador de dispositivo mvil, es deseable que cada columna se ocupe el 100% del ancho, es decir, tenga un tamao 12 para cada elemento de la grilla, para lo cual se podra definir la fila de la siguiente forma:
              .cajon.color-acento-botones.p-4.my-4
