    p Como se mencion antes, existen etiquetas que definen la estructura de una pgina como lo son &lt;body>&lt;/body>, &lt;header>&lt;/header>, &lt;main>&lt;/main>, &lt;section>&lt;/section>, &lt;aside>&lt;/aside>, &lt;footer>&lt;/footer>, por mencionar los principales, la idea general es poder disear para diferentes pantallas como se muestra en la siguiente imagen:
    
